@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap');




.main-placeorder-container{
    display: flex;
    justify-content: space-evenly;
    flex-direction: column;
    margin-top: 1rem;
    padding: 0.5rem 0.5rem;
}

.order{
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    margin: 0.5rem;
    width: auto;
}

.summary > ul > li{
    list-style-type: none;
    text-align: left;
    padding: 10px 10px;
    font-family: 'Roboto';
    letter-spacing: 1px;
    font-weight: 500;
}

.order-address> ul > li{
    list-style-type: none;
    text-align: left;
    padding: 10px 10px;
    font-family: 'Roboto';
    letter-spacing: 1px;

}


.place-order-image{
    width: 3rem;
}


.order-payment > ul > li{
    list-style-type: none;
    text-align: left;
    padding: 10px 10px;
    font-family: 'Roboto';
    letter-spacing: 1px;
}

.cart-items{
    font-family: 'Roboto';
    letter-spacing: 1px;
}

.place-order-btn{
    width: 100%;
    font-family: 'Roboto';
    font-size: 18px;
    letter-spacing: 2px;
    height: 3rem;
}
