
.top-header-container{
    background-color: #1B262C;
    display: flex;
    padding: 1rem 1rem;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
}

.logo-container{
    display: flex;
    margin-left: 1rem;
}

.amazon-logo{
    height: 2rem;
    width: auto;
    margin-top: 0.5rem;
}


.main-search-bar{
    width: 550px;
    height: 2.5rem;
    cursor: pointer;
    border: none;
    border-radius: 1.5rem;
    background-color: #EAEDED;
    padding: 1rem 1rem;
    font-size: 15px;
    font-family: 'Poppins', sans-serif;
}

.main-search-bar:focus{
    outline: none;
}


.cart-icon{
    font-size: 28px;
    color: lightgray;
}

.signin-cart-container{
    display: flex;
    color: white;
    align-items: center;
    gap: 1rem;
    padding-right: 1rem;
    font-family: 'Poppins', sans-serif;
    font-size: 15px;
}

.sign-links{
    letter-spacing: 1px;
    color: white;
}

.sign-links:hover{
    color: lightgray;
    text-decoration: none;
}



@media screen and (max-width: 960px){
    .top-header-container{
        display: flex;
        gap: 1rem;
        height: auto;
        width: 100%;
        justify-content: center;

    }

    .search-bar-container{
        width: auto;
    }

    .main-search-bar{
        width: auto;
    }

    .signin-cart-container{       
        display: flex;

    }
}
.second-header-container{
    background-color: #232F3E;
    height: auto;
    align-items: center;
    display: flex;
    justify-content: start;
}

.menu-bar-container{
    display: flex;
    justify-content: start;
    margin-left: 1rem;
    color: white;
}
.menu-bar{
    font-size: 2rem;
    color: white;
    cursor: pointer;
}

.top-nav-data-container{
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    padding: 0.75rem 0.75rem;
    gap: 1rem;
    padding-left: 2rem;
}

@media screen and (max-width: 960px){
    .second-header-container{
        display: flex;
        flex-direction: row;
        align-items: center;
        height: auto;
    }
    
    .top-nav-data-container{
        display: flex;
        flex-wrap: wrap;
        justify-content: start;
    }
}



.nav-links{
    text-decoration: none;
    color: white;
    font-size: 13px;
    letter-spacing: 0.5px;
    font-family: 'Poppins', sans-serif;
}

.nav-links:hover{
    text-decoration: none;
    color: lightgray;
}


/*****************Sidebar****************************/

.nav-menu{
        display: flex;
        flex-direction: column;
        width: 30%;
        height: 100vh;
        position: fixed;
        left: -100%;
        transition: all 0.3s ease-out;
}

.nav-menu.active{
    left: 0;
    transition: all 0.5s ease-out;
    background: #ffffff;
    z-index: 1;
    top: 0;
    height: 100vh;
    overflow-y: scroll;
}

.sidebar-profile-container{
    position: fixed;
    height: 2rem;
    padding: 10px;
    width: auto; 
    background-color: #232F3E;
}

.nav-item{
    padding: 15px;
    list-style-type: none;
}

.nav-item:hover{
    background-color: #EAEDED;
    cursor: pointer;
}



.top-divider{
    background-color: #93D600;
    height: 1.75rem;
    align-items: center;
    display: flex;
    font-size: 14px;    
    color: white;
    font-weight: 500;
    font-family: 'Roboto';
    letter-spacing: 1px;
}
