.main-footer-container{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    
}

.top-footer-container{
    background-color: #37475A;
    height: 3rem;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}

.top-footer-container > span{
    color: #fff;
    cursor: pointer;
}

.bottom-footer-container{
    background-color: #131A22;
    height: auto;
    display: flex;
    width: 100%;
    padding-top: 3rem;
    padding-bottom: 3rem;
    flex-wrap: wrap;
    gap: 4rem;
    justify-content: center;
}

.bottom-footer-column{
    display: flex;
    width: auto;
    justify-content: center;
}

.bottom-footer-column > ul > li{
    list-style-type: none;
    padding: 5px;
    text-align: left;
}

.bottom-footer-column > form > ul > li  {
    list-style-type: none;
    padding: 5px;
    text-align: left;
}

.footer-social-media-icons{
    color: #8C9196;
    font-size: 1.5rem;
    gap: 1rem;
    display: flex;
    cursor: pointer;
}

.footer-details{
    color: #8C9196;
}

.footer-links{
    color: #8C9196;
    font-size: 14px;
    font-family: 'Poppins', sans-serif;
}

.footer-links:hover{
    text-decoration: none;
    color: #FFFFE0;
}

.footer-column-title{
    font-size: 18px;
    color: #FFFFE0;
    font-family: 'Poppins', sans-serif;
}

.newsletter-field {
    height: 2.5rem;
    width: 225px;
    padding: 10px;
    font-size: 14px;
    border-radius: 5px;
    border: none;
    font-family: 'Poppins', sans-serif;
    border: 1px solid white;
}

.newsletter-btn{
    height: 2.5rem;
    width: 100%;
    margin-top: 0.5rem;
    border: none;
    font-size: 16px;
    background-color: #47991F;
    color: white;
    font-family: 'Poppins', sans-serif;
    border-radius: 5px;
}

.footer-end-container{
    display: flex;
    flex-wrap: wrap;
    height: auto;
    justify-content: center;
    align-items: center;
    padding: 0.5rem 0.5rem;
}

@media screen and (max-width: 700px){
    .bottom-footer-container{
        display: flex;
        justify-content: start;
        gap: 2rem;
    }
    .bottom-footer-column{
        display: flex;
        justify-content: center;
    }
    .footer-end-container{
        height: 6rem;
        display: flex;
        align-items: center;
        justify-content: center;
    }
}

