@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Staatliches&display=swap');

.main-cart-container{
    background-color: #EDF1F2;
    padding: 1rem 1rem;
    font-family: 'Poppins';
    display: flex;
    flex-wrap: wrap;
    gap: 1rem;
    justify-content: center;
    font-size: 15px;
}
.cart-container{
    display: flex;
    border-radius: 5px;
    flex-direction: column;
    justify-content: center;
    padding: 1rem 1rem;
    align-items: center;
    background-color: white;
}

.empty-cart-container{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}

.empty-cart-status{
    font-size: 1.2rem;
    font-family: "Roboto";
    font-weight: bold;
}
.empty-cart-btn-container{
    display: flex;
    margin: 1.5rem;
}

.empty-cart-btn-container > button{
    border-radius: 2px;
    padding: 5px 15px;
    border: none;
    margin: 0.5rem;
    background-color: #F5F6F9;
    border: 1px solid #ADB1B8;
}

.cart-list-container{
    display: flex;
    cursor: pointer;
    flex-wrap: wrap;
    flex-direction: row;
    align-items: center;
    gap: 1rem;
    padding: 1rem;
    width: 500px;
    border-bottom: 1px solid lightgray;
    justify-content: start;
}

.cart-list-product-quantity{
    width: 3rem;
    border-radius: 5px;
    padding-left: 10px;
}

.cart-list-product-quantity:focus{
    outline: none;
}


.delete-item-icon{
    color: gray;
    font-size: 18px;
}

.delete-item-icon:hover{
    color: crimson;
}

.cart-total-container{
    background-color: white;
    padding: 1rem 1rem;
    height: min-content;
    display: flex;
    justify-content: center;
    border-radius: 5px;
    gap: 1rem;
}

.cart-btn{
    padding: 1rem 1rem;
    border: none;
    text-transform: uppercase;
    font-size: 16px;
    letter-spacing: 1px;
    border-radius: 5px;
    font-weight: 600;
    color: white;
}

.cart-btn:hover{
    opacity: 0.9;
}


@media screen and (max-width: 800px) {
    .cart-list-container{
        width: 100%;
    }

    .cart-total-container{
        width: 100%;
    }
    .cart-btn-container{
        display: flex;
        flex-direction: column;
    }
    
}
