@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');

.main-products-container{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    background-color: #EDF1F2;
    gap: 1rem;
    padding: 0.5rem 0.5rem;
    padding-top: 1.5rem;
    padding-bottom: 1.5rem;
}


.single-card{
    width: 12rem;
}

.card-img{
    height: auto;
    padding: 1rem 1rem;
}

.product-container{
    background-color: #EDF1F2;
    display: flex;
    justify-content: center;
    padding: 1rem 1rem;
}

.single-product-container{
    display: flex;
    border-radius: 5px;
    width: 100%;
    padding: 1rem 1rem;
    justify-content: space-evenly;
    flex-wrap: wrap;
    border: 1px solid lightgray;
    background-color: white;
    font-family: "Poppins";
}

.single-product-details-container{
    display: flex;
    flex-direction: column;
    justify-content: start;
    padding: 1rem 1rem;
    text-align: left;
    gap: 0.5rem;
    width: 400px;
}

.single-product-image{
    width: 25rem;
}

.single-product-screen-title{
    color: #232F3E;
}

.single-product-screen-title:hover{
    text-decoration: none;
    color: #C45500;
}

.single-product-description{
    font-size: 14px;
    letter-spacing: 0.5px;
    text-align: left;
}

.single-product-screen-price{
    font-size: 2rem;
    color: #E5A00D;
}

.sizes{
    height: 2rem;
    width: 2rem;
    background-color: #232F3E;
    border-radius: 3rem;
    display: flex;
    font-size: 15px;
    opacity: 0.9;
    align-items: center;
    justify-content: center;
    color: white;
    text-align: center;
}


.single-product-view-quantity{
    width: 4rem;
    height: 2rem;
    text-align: center;
    border-radius: 5px;
    cursor: pointer;
    font-size: 15px;
    padding-left: 0.5rem;
}

.single-product-view-quantity:focus{
    outline: none;
}

.add-cart-container{
    padding: 1rem 1rem;
    display: flex;
    justify-content: start;
    text-align: start;
}

.add-cart-btn{
    padding: 1rem 1rem;
    text-transform: uppercase;
    width: 15rem;
    border: none;
    font-weight: bold;
    letter-spacing: 1px;
    border-radius: 5px;
    font-size: 18px;
    justify-content: center;
    display: flex;
    gap: 5px;
    align-items: center;
    color: white;
    background-color: #47991F;
}

.add-cart-btn:hover{
    opacity: 0.9;
}

@media screen and (max-width: 800px) {

    .single-product-container{
        justify-content: start;
    }

    .single-product-image{
        width: 100%;
    }
    
}



