@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap');

.main-signin-container{
    display: flex;
    justify-content: center;
    padding: 2rem 2rem;
}


.form-labels{
    text-align: left;
    font-family: "Roboto";
    font-weight: bold;
    width: 100%;
}

.form-signin-btn{
    width: 100%;
    background-color: #F1C453;
    border: 1px solid #9C7E31;
    color: black;
    font-family: "Roboto";
    font-size: 16px;
}



.signin-createnew-container{
    margin-top: 1rem;
}
                        
.newcustomer-label{
    font-size: 14px;
    font-weight: bold;
    font-family: "Roboto";
}


@media (min-width: 776px) { 
    .form-container{
        width: 100%;
    }
}