.top-header-container{
    background-color: #1B262C;
    display: flex;
    padding: 1rem 1rem;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
}

.logo-container{
    display: flex;
    margin-left: 1rem;
}

.amazon-logo{
    height: 2rem;
    width: auto;
    margin-top: 0.5rem;
}


.main-search-bar{
    width: 550px;
    height: 2.5rem;
    cursor: pointer;
    border: none;
    border-radius: 1.5rem;
    background-color: #EAEDED;
    padding: 1rem 1rem;
    font-size: 15px;
    font-family: 'Poppins', sans-serif;
}

.main-search-bar:focus{
    outline: none;
}


.cart-icon{
    font-size: 28px;
    color: lightgray;
}

.signin-cart-container{
    display: flex;
    color: white;
    align-items: center;
    grid-gap: 1rem;
    gap: 1rem;
    padding-right: 1rem;
    font-family: 'Poppins', sans-serif;
    font-size: 15px;
}

.sign-links{
    letter-spacing: 1px;
    color: white;
}

.sign-links:hover{
    color: lightgray;
    text-decoration: none;
}



@media screen and (max-width: 960px){
    .top-header-container{
        display: flex;
        grid-gap: 1rem;
        gap: 1rem;
        height: auto;
        width: 100%;
        justify-content: center;

    }

    .search-bar-container{
        width: auto;
    }

    .main-search-bar{
        width: auto;
    }

    .signin-cart-container{       
        display: flex;

    }
}
.second-header-container{
    background-color: #232F3E;
    height: auto;
    align-items: center;
    display: flex;
    justify-content: start;
}

.menu-bar-container{
    display: flex;
    justify-content: start;
    margin-left: 1rem;
    color: white;
}
.menu-bar{
    font-size: 2rem;
    color: white;
    cursor: pointer;
}

.top-nav-data-container{
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    padding: 0.75rem 0.75rem;
    grid-gap: 1rem;
    gap: 1rem;
    padding-left: 2rem;
}

@media screen and (max-width: 960px){
    .second-header-container{
        display: flex;
        flex-direction: row;
        align-items: center;
        height: auto;
    }
    
    .top-nav-data-container{
        display: flex;
        flex-wrap: wrap;
        justify-content: start;
    }
}



.nav-links{
    text-decoration: none;
    color: white;
    font-size: 13px;
    letter-spacing: 0.5px;
    font-family: 'Poppins', sans-serif;
}

.nav-links:hover{
    text-decoration: none;
    color: lightgray;
}


/*****************Sidebar****************************/

.nav-menu{
        display: flex;
        flex-direction: column;
        width: 30%;
        height: 100vh;
        position: fixed;
        left: -100%;
        transition: all 0.3s ease-out;
}

.nav-menu.active{
    left: 0;
    transition: all 0.5s ease-out;
    background: #ffffff;
    z-index: 1;
    top: 0;
    height: 100vh;
    overflow-y: scroll;
}

.sidebar-profile-container{
    position: fixed;
    height: 2rem;
    padding: 10px;
    width: auto; 
    background-color: #232F3E;
}

.nav-item{
    padding: 15px;
    list-style-type: none;
}

.nav-item:hover{
    background-color: #EAEDED;
    cursor: pointer;
}



.top-divider{
    background-color: #93D600;
    height: 1.75rem;
    align-items: center;
    display: flex;
    font-size: 14px;    
    color: white;
    font-weight: 500;
    font-family: 'Roboto';
    letter-spacing: 1px;
}


.menu-item{
    text-align: left;
    cursor: pointer;
    padding: 1rem 1rem;
}

.menu-item:hover{
    background-color: #EAEDED;
}

.menu{
    position: fixed;
    overflow-y: scroll;
    top: 0;
    bottom: 0;
    left: 0;
    background-color: white;
    z-index: 1;
    display: flex;
    flex-direction: column;
    grid-gap: 25px;
    gap: 25px;
    min-width: 280px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.side-bar-header-links {
    text-decoration: none;
    font-size: 15px;
    font-family: 'Poppins', sans-serif;
    font-weight: bold;
    text-transform: uppercase;
    letter-spacing: 1px;
    text-decoration: none;
    color: #191919;
}

.menu::-webkit-scrollbar{
    display: none;
}

.side-menu-profile-container{
    display: flex;
    justify-content: space-between;
    padding-bottom: 15px;
    background-color: #232F3E;
    font-family: 'Poppins', sans-serif;
    padding: 1rem 1rem;
    min-width: 280px;
    position: fixed;
    color: lightgray;
    border-bottom: 2px solid lightgray;
}

.user-profile{
    font-size: 2rem;
    margin-right: 5px;
}

.user-info{
    font-size: 14px;
    letter-spacing: 1px;
    font-weight: 500;
    align-items: center;
}

.close-menu-icon{
    font-size: 1.5rem;
    cursor: pointer;
    font-weight: bold;
}

.close-menu-icon:hover{
    color: white;
    transform: scale(1.25);
}

.side-menu-container{
    margin-top: 1rem;
}

.side-nav-links{
    font-size: 15px;
    font-family: 'Poppins', sans-serif;
    color: #232F3E;
    margin-left: 1rem;
}

.side-nav-links:hover{
    text-decoration: none;
    color: #232F3E;
}


.main-products-container{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    background-color: #EDF1F2;
    grid-gap: 1rem;
    gap: 1rem;
    padding: 0.5rem 0.5rem;
    padding-top: 1.5rem;
    padding-bottom: 1.5rem;
}


.single-card{
    width: 12rem;
}

.card-img{
    height: auto;
    padding: 1rem 1rem;
}

.product-container{
    background-color: #EDF1F2;
    display: flex;
    justify-content: center;
    padding: 1rem 1rem;
}

.single-product-container{
    display: flex;
    border-radius: 5px;
    width: 100%;
    padding: 1rem 1rem;
    justify-content: space-evenly;
    flex-wrap: wrap;
    border: 1px solid lightgray;
    background-color: white;
    font-family: "Poppins";
}

.single-product-details-container{
    display: flex;
    flex-direction: column;
    justify-content: start;
    padding: 1rem 1rem;
    text-align: left;
    grid-gap: 0.5rem;
    gap: 0.5rem;
    width: 400px;
}

.single-product-image{
    width: 25rem;
}

.single-product-screen-title{
    color: #232F3E;
}

.single-product-screen-title:hover{
    text-decoration: none;
    color: #C45500;
}

.single-product-description{
    font-size: 14px;
    letter-spacing: 0.5px;
    text-align: left;
}

.single-product-screen-price{
    font-size: 2rem;
    color: #E5A00D;
}

.sizes{
    height: 2rem;
    width: 2rem;
    background-color: #232F3E;
    border-radius: 3rem;
    display: flex;
    font-size: 15px;
    opacity: 0.9;
    align-items: center;
    justify-content: center;
    color: white;
    text-align: center;
}


.single-product-view-quantity{
    width: 4rem;
    height: 2rem;
    text-align: center;
    border-radius: 5px;
    cursor: pointer;
    font-size: 15px;
    padding-left: 0.5rem;
}

.single-product-view-quantity:focus{
    outline: none;
}

.add-cart-container{
    padding: 1rem 1rem;
    display: flex;
    justify-content: start;
    text-align: start;
}

.add-cart-btn{
    padding: 1rem 1rem;
    text-transform: uppercase;
    width: 15rem;
    border: none;
    font-weight: bold;
    letter-spacing: 1px;
    border-radius: 5px;
    font-size: 18px;
    justify-content: center;
    display: flex;
    grid-gap: 5px;
    gap: 5px;
    align-items: center;
    color: white;
    background-color: #47991F;
}

.add-cart-btn:hover{
    opacity: 0.9;
}

@media screen and (max-width: 800px) {

    .single-product-container{
        justify-content: start;
    }

    .single-product-image{
        width: 100%;
    }
    
}




.main-cart-container{
    background-color: #EDF1F2;
    padding: 1rem 1rem;
    font-family: 'Poppins';
    display: flex;
    flex-wrap: wrap;
    grid-gap: 1rem;
    gap: 1rem;
    justify-content: center;
    font-size: 15px;
}
.cart-container{
    display: flex;
    border-radius: 5px;
    flex-direction: column;
    justify-content: center;
    padding: 1rem 1rem;
    align-items: center;
    background-color: white;
}

.empty-cart-container{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}

.empty-cart-status{
    font-size: 1.2rem;
    font-family: "Roboto";
    font-weight: bold;
}
.empty-cart-btn-container{
    display: flex;
    margin: 1.5rem;
}

.empty-cart-btn-container > button{
    border-radius: 2px;
    padding: 5px 15px;
    border: none;
    margin: 0.5rem;
    background-color: #F5F6F9;
    border: 1px solid #ADB1B8;
}

.cart-list-container{
    display: flex;
    cursor: pointer;
    flex-wrap: wrap;
    flex-direction: row;
    align-items: center;
    grid-gap: 1rem;
    gap: 1rem;
    padding: 1rem;
    width: 500px;
    border-bottom: 1px solid lightgray;
    justify-content: start;
}

.cart-list-product-quantity{
    width: 3rem;
    border-radius: 5px;
    padding-left: 10px;
}

.cart-list-product-quantity:focus{
    outline: none;
}


.delete-item-icon{
    color: gray;
    font-size: 18px;
}

.delete-item-icon:hover{
    color: crimson;
}

.cart-total-container{
    background-color: white;
    padding: 1rem 1rem;
    height: -webkit-min-content;
    height: -moz-min-content;
    height: min-content;
    display: flex;
    justify-content: center;
    border-radius: 5px;
    grid-gap: 1rem;
    gap: 1rem;
}

.cart-btn{
    padding: 1rem 1rem;
    border: none;
    text-transform: uppercase;
    font-size: 16px;
    letter-spacing: 1px;
    border-radius: 5px;
    font-weight: 600;
    color: white;
}

.cart-btn:hover{
    opacity: 0.9;
}


@media screen and (max-width: 800px) {
    .cart-list-container{
        width: 100%;
    }

    .cart-total-container{
        width: 100%;
    }
    .cart-btn-container{
        display: flex;
        flex-direction: column;
    }
    
}

.main-footer-container{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    
}

.top-footer-container{
    background-color: #37475A;
