}

.bottom-footer-container{
    background-color: #131A22;
    height: auto;
    display: flex;
    width: 100%;
    padding-top: 3rem;
    padding-bottom: 3rem;
    flex-wrap: wrap;
    grid-gap: 4rem;
    gap: 4rem;
