
.menu-item{
    text-align: left;
    cursor: pointer;
    padding: 1rem 1rem;
}

.menu-item:hover{
    background-color: #EAEDED;
}

.menu{
    position: fixed;
    overflow-y: scroll;
    top: 0;
    bottom: 0;
    left: 0;
    background-color: white;
    z-index: 1;
    display: flex;
    flex-direction: column;
    gap: 25px;
    min-width: 280px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.side-bar-header-links {
    text-decoration: none;
    font-size: 15px;
    font-family: 'Poppins', sans-serif;
    font-weight: bold;
    text-transform: uppercase;
    letter-spacing: 1px;
    text-decoration: none;
    color: #191919;
}

.menu::-webkit-scrollbar{
    display: none;
}

.side-menu-profile-container{
    display: flex;
    justify-content: space-between;
    padding-bottom: 15px;
    background-color: #232F3E;
    font-family: 'Poppins', sans-serif;
    padding: 1rem 1rem;
    min-width: 280px;
    position: fixed;
    color: lightgray;
    border-bottom: 2px solid lightgray;
}

.user-profile{
    font-size: 2rem;
    margin-right: 5px;
}

.user-info{
    font-size: 14px;
    letter-spacing: 1px;
    font-weight: 500;
    align-items: center;
}

.close-menu-icon{
    font-size: 1.5rem;
    cursor: pointer;
    font-weight: bold;
}

.close-menu-icon:hover{
    color: white;
    transform: scale(1.25);
}

.side-menu-container{
    margin-top: 1rem;
}

.side-nav-links{
    font-size: 15px;
    font-family: 'Poppins', sans-serif;
    color: #232F3E;
    margin-left: 1rem;
}

.side-nav-links:hover{
    text-decoration: none;
    color: #232F3E;
}

