@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@500&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Oswald:wght@500&display=swap');

.main-admin-product-container{
    display: flex;
    justify-content: center;
    flex-direction: column;
}

.main-admin-view-top-container{
    display: flex;
    flex-wrap: wrap;
    gap: 1rem;
    margin: 1rem 1rem;
    justify-content: space-between;
}

.main-admin-product-top-container > div > span{
    font-size: 1.5rem;
    color: white;
    letter-spacing: 5px;
    font-family: 'Oswald', sans-serif;
}
.admin-product-table-container{
    overflow-y: scroll;
    height: 20rem;
    margin: 1rem 1rem;
    cursor: pointer;
}

.admin-product-table-container > div{
    margin: 1rem;
}

.admin-product-table-container > div >span{
    font-size: 1.5rem;
    font-family: 'Roboto';
    color: dimgrey;
}
.admin-product-view-table > thead > tr > th {
    font-family: 'Roboto';
    font-size: 14px;
    color: dimgray;
    letter-spacing: 1.1px;
}

.admin-product-img{
    width: 5rem;
}
